.complete-note-container{
    width: 100%;
    padding: 10px 15px;
    border-radius: 10px;
    box-shadow: 1px 1px 5px 4px rgb(218, 224, 230);
    display: flex;
    /* border: 1px solid violet; */
    flex-direction: column;
}
.title-section{
    width: 100%;
    display: flex;
    align-content: center;
    justify-content: center;
    padding-bottom: 15px;
}
.small{
    font-size: 15px;
}
.large{
    font-size: 18px;
}
.small-icon{
    font-size: 18px;
    padding: 5px;
}
.hover-effect{
    width: calc(10%-20px);
    height: auto;
    padding: 5px 8px;
    cursor: pointer;
}
.hover-effect:hover{
    background: rgb(230, 236, 245);
    border-radius: 50%;
}
.text-field{
    width: 95%;
    outline: none;
    border: none;
    resize: none;
    font-family: 'Cabin', sans-serif;
    /* border: 1px solid violet; */
    opacity: 0.9;
}

.bottom-options{
    width: 100%;
    display: flex;
}

.left-side-options{
    width: 70%;
    display: flex;
    align-content: center;
    justify-content: space-between;
}

.right-side-options{
    width:25%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.close-btn{
    outline:none;
    border:none;
    background: none;
    padding: 8px 20px;
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    color: rgb(92, 92, 92);
}

.close-btn:hover{
    background: rgb(235,235,255);
    border-radius: 5px;
    cursor: pointer;
}