  /* content body styles*/
  
  .content-body{
    width: 100%;
    height: auto;
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 70px auto;
  }
  
  /* left side navigation styles */
  
  
  /* Edit Label Dialog */
  
  .edit-labels-dialog{
    width: 300px;
    height: 170px;
    box-shadow: 1px 1px 5px 4px rgb(218, 224, 230);
    background: rgb(255,255,255);
    position: fixed;
    top:25%;
    left: 25%;
    bottom: 25%;
    right: 25%;
    margin: auto;
    z-index: 3;
    display: none;
    grid-template-columns: auto;
    grid-template-rows: auto auto auto;
    font-family: 'Cabin', sans-serif;
  }
  
  .label-title{
    width: 100%;
    padding: 20px 0 0 15px;
  }
  
  .label-body{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    border-bottom: 1px solid rgb(205, 207, 209);
  }
  
  .label-text-field>input{
    outline: none;
    border: none;
  }
  
  .label-done-btn{
    border: none;
    background: rgb(255,255,255);
    padding: 15px 25px 0 0;
    color: rgb(79, 84, 92);
    font-size: 14px;
    text-align: right;
    cursor: pointer;
  }
  
  
  /* media queries */
  @media only screen and (max-width:465px){
    .content-body{
        grid-template-columns: 70px 340px;
    }
    .navbar{
        width: 450px;
    }
  }
  
  