.title-container{
    width: 100%;
    height: 45px;
    border-radius: 10px;
    box-shadow: 1px 1px 5px 4px rgb(218, 224, 230);
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: auto 150px;
    align-content: stretch;
    /* border: 1px solid black; */
}

.side-options{
    /* border: 1px solid violet; */
    display: flex;
    justify-content: space-around;
    /* border: 1px solid violet; */
}

.side-options>div{
    padding: 10px 12px;
    /* border: 1px solid violet; */
}
.side-options>div:hover{
    background: rgb(230, 236, 245);
    border-radius: 50%;
    cursor: pointer;
}

.note-text-section{
    display: flex;
    align-items: center;
    /* border: 1px solid violet; */
}
.note-text-section>input{
    width: 100%;
    outline: none;
    border: none;
    padding: 0 10px;
    font-size: 15px;
    font-family: 'Cabin', sans-serif;
    /* border: 1px solid violet; */
    opacity: 0.9;
}