/* navbar styles */
.navbar{
    width: calc(100%-20px);
    height: calc(64px);
    padding: 0 10px;
    grid-template-rows: auto;
    grid-template-columns: 250px auto 250px 100px;
    gap: 0 2px;
    display: grid;
    align-content: center;
    border-bottom: 1px solid rgb(205, 207, 209);
  }
  
  /* left side divs styles*/
  .left-side-div, .right-side-div{
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  .menu-bar-icon{
    width: calc(10%-24px);
    height: auto;
    padding: 10px 12px;
    cursor: pointer;
  }
  .logo-div{
    width: calc(10%-24px);
    height: auto;
    padding: 10px 12px;
    display: flex;
    align-items: center;
    flex: 1 3 auto;
  }
  .logo{
    width: 32px;
    height: 32px;
  }
  .logo-text{
    font-size: 23px;
    color: rgb(79, 84, 92);
    margin-left: 10px;
  }
  .material-icons-outlined{
    color: rgb(79, 84, 92);
  }
  .menu-bar-icon:hover{
    background: rgb(230, 236, 245);
    border-radius: 50%;
  }
  
  /* center div styles */
  .middle-div{
    width: 100%;
    height: 100%;
  }
  .search-div{
    width: calc(100%-10px);
    height: calc(100%);
    padding: 0 5px;
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 50px auto;
    align-content: center;
    background: rgb(230, 236, 245);
    border-radius: 0.5rem;
  }
  .search-icon, .search-bar{
    width: calc(100%-24px);
    height: auto;
    padding: 10px 12px;
  }
  .search-icon:hover{
    background: rgb(218, 224, 230);
    border-radius: 50%;
    cursor: pointer;
  }
  .search-bar{
    font-size: 16px;
    border: none;
    outline: none;
    background: transparent;
  }
  .search-bar:focus{
    background: none;
  }
  
  .search-div-effects{
    transition: 200ms;
    background: none;
    box-shadow: 1px 1px 5px 2px rgb(218, 224, 230);
  }
  
  
  /* right side divs styles*/
  
  .right-side-div, .right-most-div{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: flex-start;
    background: rgb(255,255,255);
  }
  
  .more-apps, .settings, .grid-view, .list-view, .refresh, .login-div, .mini-search{
    width: calc(10%-24px);
    height: auto;
    padding: 10px 12px;
  }
  
  .mini-search{
    display: none;
  }
  .more-apps:hover, .settings:hover, .grid-view:hover, .list-view:hover, .refresh:hover, .login-div:hover, .mini-search:hover{
    background: rgb(218, 224, 230);
    border-radius: 50%;
    cursor: pointer;
  }
  
    /* media queries */
  @media only screen and (max-width:500px){
    .navbar{
        height: 56px;
    }
  }
  
  @media only screen and (max-width:795px){
    .navbar{
        grid-template-columns: auto 0% auto auto;
    }
    .search-div{
        display: none;
    }
    .mini-search{
        display: block;
    }
    .right-side-div, .right-most-div{
        justify-content: space-around;
    }
    .title-container{
        width: 70%;
    }
  }
  @media only screen and (max-width:465px){
    .content-body{
        grid-template-columns: 70px 340px;
    }
    .navbar{
        width: 450px;
    }
  }

  @media only screen and (max-width:1024px){
    .navbar{
        grid-template-columns: 250px auto auto auto;
    }
  }