/* right side navigation styles */
  
.right-side-content-div{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 40px;
    /* border: 1px solid violet; */
}

.title-bar{
    width: 50%;
    display: flex;
    justify-content: center;
}

.inner-container{
    padding-top: 30px;
    display: flex;
    flex-wrap: wrap;
}

.showNothing{
    display: none;
}
@media only screen and (max-width:500px){
    .right-side-content-div{
        width: 500px;
    }
  }

@media only screen and (max-width:900px){
    .title-bar{
        width: 450px;
    }
}