.showLess{
    display: none;
}
.showFull{
  display: "block";
}
.single-navigation{
  width: 280px;
  height: 100%;
  /* box-shadow: 2px 0 5px 0 rgb(205, 207, 209); */
}

.no-hovers{
  width: 50px;
  height: 100%;
  margin-left: 10px;
  border-radius: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.hovers{
  width: 250px;
  height: 100%;
  padding-left: 23px;
  border-radius: 0 25px 25px 0;
  font-size: 14px;
  font-family: 'Cabin', sans-serif;
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: auto auto;
  align-content: center;
  justify-content: flex-start;
  column-gap: 20%;
}

.notes:hover, .reminders:hover, .edit-label:hover, .archive:hover, .bin:hover{
  background: rgb(230, 236, 245);
  cursor: pointer;
}
#active{
  background: rgb(255, 232, 186);
  cursor: pointer;
}

.left-side-navigation-div{
  /* border: 1px solid violet; */
  height: 250px;
  margin: 10px 0 0 0;
  display: flex;
  z-index: 2;
  background: white;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}